<template>
    <div class="new-list">
        <div class="custom-card">
            <div class="custom-card-header">
                {{$t('listNews.action')}}
            </div>
            <div class="custom-card-content">
                <a-button class="custom-button">
                    <router-link :to="createNewsLink">
                        {{$t('listNews.createBtn')}}
                    </router-link>
                </a-button>
            </div>
        </div>
        <div class="new-list-content custom-card">
            <div class="custom-card-header">
                {{$t('listNews.list')}}
            </div>
            <div class="custom-card-content">
                <a-table
                        :columns="columns"
                        :data-source="data"
                        bordered
                        :pagination="{pageSize: 5}"
                >
                    <span slot="customTitle">{{$t('listNews.title')}}</span>
                    <span slot="customDes">{{$t('listNews.description')}}</span>
                    <span slot="customDate">{{$t('listNews.date')}}</span>
                    <span slot="customStatus">{{$t('listNews.status')}}</span>
                    <span slot="customAction">{{$t('listNews.actionBtn')}}</span>
                    <span slot="date" slot-scope="date">
                        {{getDatePost(date)}}
                    </span>
                    <span slot="status" slot-scope="status">
                        {{getStatus(status)}}
                    </span>
                    <span slot="action" slot-scope="record" class="action-button">
                        <a-tooltip>
                            <template slot="title">
                                {{$t('listNews.edit')}}
                            </template>
                            <a-button>
                                <router-link :to="editLink.replace(':id', record.key)">
                                    <a-icon type="edit" />
                                </router-link>
                            </a-button>
                        </a-tooltip>
                        <a-tooltip>
                            <template slot="title">
                                {{$t('listNews.delete')}}
                            </template>
                            <a-button @click="() => deleteItem(record.key)">
                                <a-icon type="delete" />
                            </a-button>
                        </a-tooltip>
                    </span>
                </a-table>
            </div>
        </div>
    </div>
</template>

<script>
    import data from "../../constant";
    import links from "../../router/links";
    import moment from 'moment';
    import status from "../../constant/status";
    import i18n from "../../plugins/i18n";
    export default {
        name: "NewsList",
        data() {
            return {
                columns: data.columns,
                createNewsLink: links.CREATE_NEWS,
                editLink: links.EDIT_NEWS,
            }
        },
        computed: {
            data: function () {
                return this.$store.state.news;
            }
        },
        methods: {
            getDatePost(date) {
                return moment(date).format('YYYY/MM/DD HH:mm');
            },
            deleteItem(id) {
                const self = this;
                this.$confirm({
                    title: i18n.t('listNews.deleteItem'),
                    content: i18n.t('listNews.deleteContent'),
                    okText: i18n.t('button.yes'),
                    okType: 'danger',
                    cancelText: i18n.t('button.no'),
                    onOk() {
                        self.$store.dispatch('setSpinning', true);
                        setTimeout(() => {
                            self.$store.dispatch('setSpinning', false);
                            let index = self.$store.state.news.findIndex(item => item.key === id);
                            self.$store.dispatch('deleteNews', index);
                        }, 1000);
                    }
                });
            },
            moment() {
                return moment();
            },
            getStatus(item) {
                switch (item) {
                    case status.NEW:
                        return i18n.t('listNews.new');
                    case status.POST:
                        return i18n.t('listNews.post');
                    case status.CANCEL:
                        return i18n.t('listNews.cancel');
                    default:
                        return
                }
            }
        },
    }
</script>

<style scoped lang="scss">
    @import "../../assets/scss/variables";
    .new-list {
        .action-button {
            display: flex;
            align-items: center;
            justify-content: center;
            & > button {
                width: 32px;
                display: flex;
                align-items: center;
                justify-content: center;
                &:not(:first-child) {
                    margin-left: 5px;
                }
                & > a {
                    transition: 0.1s!important;
                    & > i {
                        transition: 0.1s!important;
                    }
                }
                &:hover {
                    background: $gradient;
                    color: #fff;
                    border-color: #eee;
                }
            }
        }
    }
</style>